<template>
  <div class="header-bar">
    <h1>
      图书推荐系统
      <router-link class="goback" v-if="true" :to="'/'">首页</router-link>
      <!-- <a href="#"  >返回</a> -->
    </h1>
  </div>
</template>

<script>
export default {
  name: 'header-bar',
  data () {
    return {
      showback:false
    }
  },
  created(){
    
    this.showback=this.$route.path.indexOf('book')<0
  }
}
</script>

<style lang="scss" scoped>
.header-bar {
  h1{
    display: block;
    width:100%;
    .goback{
      display: block;
      float:right;
      color:#42bd56
    }
  }
  ul {
    display: flex;
    flex: 1;
    justify-content: flex-end;
  }

  li {
    display: inline-block;
    font-size: 1.6rem;
    margin-right: 1.2rem;
  }

  .talion {
    font-size: 0;
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
    width: 2.4rem;
    height: 1.8rem;
    margin-top: 0.4rem;
  }
}
</style>



